import React, { Component } from "react";
import Current from "./Current";
import Hourly from "./Hourly";
import Daily from "./Daily";
import Selection from "./Selection";
import axios from "axios";
import dayjs from "dayjs";
import Cookies from "js-cookie";

// helper functions
import { saveHourlyData, saveDailyData, saveCurrentData } from "./helpers.js";

const querystring = require("querystring");
const api = require("../data/api.json");

class Cities extends Component {
  state = {
    timestamp: "",
    cityName: Cookies.getJSON("defaultCity") || "",
    timezone_offset: 0,
    current: {},
    hourly: {},
    daily_ME: [],
    daily_IM: [],
  };

  // fetch from open weather API
  fetchWeatherData = (city, units, lat, lon) => {
    const parameters = {
      appid: api.key,
      units: units,
      lat: lat,
      lon: lon,
      exclude: "minutely",
    };

    const url = `https://api.openweathermap.org/data/2.5/onecall?${querystring.stringify(
      parameters
    )}`;

    return axios.get(url);
  };

  // call fetch data and persist response data
  getData = async (cityName, coordinates) => {
    this.setDefaultCity(cityName);
    const lat = coordinates.lat;
    const lon = coordinates.lon;
    const date = dayjs().format("MM-DD-YY h:mma");

    const data_metric = await this.fetchWeatherData(
      cityName,
      "metric",
      lat,
      lon
    );
    const data_imperial = await this.fetchWeatherData(
      cityName,
      "imperial",
      lat,
      lon
    );

    if (data_metric && data_imperial) {
      const current = saveCurrentData(data_imperial.data, data_metric.data);

      const hourly = saveHourlyData(
        data_imperial.data.hourly,
        data_metric.data.hourly
      );
      const daily = saveDailyData(
        data_imperial.data.daily,
        data_metric.data.daily
      );

      let name = "";
      if (!Cookies.getJSON("defaultCity")) {
        name = cityName;
      } else {
        name = Cookies.getJSON("defaultCity");
      }

      this.setState({
        timestamp: date,
        cityName: name,
        current,
        hourly,
        daily,
      });
    }
  };

  setDefaultCity = (cityName) => {
    Cookies.set("defaultCity", JSON.stringify(cityName), { expires: 24 });
  };

  renderCurrent = () => {
    if (this.state.current.IM) {
      return (
        <Current
          cityName={this.state.cityName}
          current={this.state.current}
          timestamp={this.state.timestamp}
        />
      );
    }
  };

  renderHourly = () => {
    if (this.state.hourly.IM) {
      return <Hourly IM={this.state.hourly.IM} ME={this.state.hourly.ME} />;
    }
  };

  renderDaily = () => {
    if (this.state.daily_IM.temp) {
      return (
        <Daily
          daily_IM={this.state.daily_IM}
          daily_ME={this.state.daily_ME}
          timestamp={this.state.timestamp}
        />
      );
    }
  };

  componentDidMount = () => {
    if (this.state.cityName === "") {
      this.getData("New York", { lon: -74.01, lat: 40.71 });
    } else {
      this.getData(this.state.cityName, { lon: -74.01, lat: 40.71 });
    }
  };

  render() {
    return (
      <React.Fragment>
        <div className="d-flex flex-column containter-fluid bg-info align-items-center">
          <Selection getData={this.getData} />
          <div
            className="card bg-primary container"
            style={({ width: "40rem" }, { margin: "2%" })}
          >
            {this.renderCurrent()}
            {this.renderHourly()}
            {/* {this.renderDaily()} */}
          </div>
        </div>
      </React.Fragment>
    );
  }
}

export default Cities;
