import React, { Component } from "react";
import dayjs from "dayjs";

class Current extends Component {
  state = {};

  renderCurrent = () => {
    const { cityName, timestamp, current_IM: IM, current_ME: ME } = this.props;

    const timezoneOffset = IM.timezone_offset;

    var utc = require("dayjs/plugin/utc");
    dayjs.extend(utc);

    const sunrise = dayjs
      .utc((IM.sunrise + timezoneOffset) * 1000)
      .format("h:mma");

    const sunset = dayjs
      .utc((IM.sunset + timezoneOffset) * 1000)
      .format("h:mma");

    const iconURL = `http://openweathermap.org/img/w/${IM.weather[0].icon}.png`;

    return (
      <div
        className="card bg-primary container"
        style={({ width: "40rem" }, { margin: "2%" })}
      >
        <div className="d-flex flex-column align-items-center card-header">
          <h5 style={{ margin: "2%" }}>{timestamp}</h5>

          <h1 className="card-title text-left">{cityName}</h1>
          <div className="d-flex align-items-center">
            <img src={iconURL} className="img-fluid" alt="Responsive"></img>
            <h2 className="card-text text-left">{IM.weather[0].description}</h2>
          </div>

          <div className="d-flex flex-wrap">
            <h1 className="card-text text-left">{IM.temp}</h1>
            <p className="card-text text-left">˚F</p>

            <h1 className="card-text text-left"> / {ME.temp}</h1>
            <p className="card-text text-left">˚C</p>
          </div>
        </div>

        <div className="card-body">
          <div className="row">
            <div className="col-6 text-right">
              <h4 className="card-text">Feels like</h4>
            </div>
            <div className="col-6">
              <div className="d-flex flex-wrap">
                <h4>{IM.feels_like}</h4>
                <p className="card-text">˚F</p>
                <h4>/ {ME.feels_like}</h4>
                <p className="card-text">˚C</p>
              </div>
            </div>
          </div>

          <hr />

          <div className="row">
            <div className="col-6 text-right">
              {" "}
              <h4 className="card-text">Humidity</h4>
            </div>
            <div className="col-6">
              {" "}
              <h4 className="card-text">{IM.humidity}%</h4>
            </div>
          </div>

          <hr />

          <div className="row">
            <div className="col-6 text-right">
              <h4 className="card-text">Wind</h4>
            </div>
            <div className="col-6">
              {" "}
              <div className="d-flex flex-wrap">
                <h4 className="card-text text-left">{IM.wind_speed}</h4>
                <p className="card-text text-left">mph</p>
                <h4 className="card-text text-left">/ {ME.wind_speed}</h4>
                <p className="card-text text-left">kmh</p>
              </div>
            </div>
          </div>
        </div>
        <div className="card-footer row">
          <div className="col sm-6 text-right">
            <h5 className="card-text">Low</h5>
            <div className="d-flex flex-wrap justify-content-end">
              <h5>{IM.temp_min}</h5>
              <p className="card-text">˚F</p>
              <h5>/ {ME.temp_min}</h5>
              <p className="card-text">˚C</p>
            </div>
          </div>
          <div className="col sm-6 text-left">
            <h5 className="card-text text-left">High</h5>
            <div className="d-flex flex-wrap">
              <h5>{IM.temp_max}</h5>
              <p className="card-text text-left">˚F</p>
              <h5>/ {ME.temp_max}</h5>
              <p className="card-text text-left">˚C</p>
            </div>
          </div>
        </div>
        <div className="card-footer row">
          <div className="col sm-6 text-right">
            <h4 className="card-text">Sunrise: {sunrise}</h4>
          </div>
          <div className="col sm-6 text-left">
            <h4 className="card-text">Sunset: {sunset}</h4>
          </div>
        </div>
      </div>
    );
  };

  render() {
    return <React.Fragment>{this.renderCurrent()}</React.Fragment>;
  }
}

export default Current;
